import React from 'react';
import { NavLink } from 'react-router-dom';
import {CgMenuRightAlt} from 'react-icons/cg';
const links = [
    {
        name:"Home",
        url:"/"
    },
    {
        name:"About",
        url:"/about"
    },
    {
        name:"Movies",
        url:"/movie"
    },
    {
        name:"Contact",
        url:"/contact"
    }
]
const ReactNavigation =()=>{
    return( 
        <div className="c-nav">
            <div className="">
                <CgMenuRightAlt/>
            </div>
            <ul className="nav-list">
                {links.map((link,i)=>(
                    <li key="i"><NavLink to={link.url}>{link.name}</NavLink></li>
                ))}
            </ul>
        </div> 
    )
}
export default ReactNavigation;